opacity: 1;\n    }\n  }\n}\n","@import \"../../styles/variables.scss\";\n@import \"../common/typography/text.mixins.scss\";\n\nnovo-select {\n  display: block;\n  position: relative;\n  width: 100%;\n  max-width: 800px;\n  min-width: 180px;\n  cursor: pointer;\n\n  .novo-select-trigger {\n    @include novo-body-text();\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    background-color: transparent;\n    border: none;\n    border-bottom: 1px solid lighten($dark, 45%);\n    color: var(--text-main, $dark);\n    height: 2.05rem;\n    position: relative;\n    text-align: left;\n    text-shadow: none;\n    z-index: z(default);\n    cursor: pointer;\n    text-transform: none;\n    padding: 0 $spacing-sm;\n    margin-bottom: -1px;\n    -webkit-appearance: none;\n\n    &.empty {\n      color: #a9a9a9;\n    }\n    &:focus,\n    &:hover {\n      outline: none;\n    }\n    &:hover {\n      border-bottom: 1px solid lighten($dark, 15%);\n    }\n    &:focus {\n      border-bottom: 1px solid $positive;\n      i {\n        color: rgba(0, 0, 0, 0.73);\n      }\n    }\n\n    .novo-select-placeholder {\n      color: var(--form-placeholder);\n    }\n\n    i {\n      font-size: 0.8em;\n      color: $dark;\n      position: absolute;\n      right: 0px;\n    }\n  }\n  &[disabled] {\n    pointer-events: none;\n    div[type=\"button\"] {\n      color: $grey;\n    }\n  }\n}\n\n.novo-select-list {\n  &.active {\n    z-index: z(overlay);\n    max-height: 219px;\n    min-width: 200px;\n    width: 100%;\n    max-width: 800px;\n    width: inherit;\n    overflow: auto;\n    opacity: 1;\n  }\n  background-color: var(--background-bright);\n  cursor: default;\n  list-style: none;\n  overflow: auto;\n  margin: 0;\n  padding: 0;\n  width: 100%;\n  box-shadow: 0 -1px 3px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);\n  font-size: 1rem;\n  z-index: z(below);\n  opacity: 0;\n}\n\n.select-header {\n  button {\n    text-transfo