rror-color;\n      @include tooltip-pointer($tooltip-error-color);\n    }\n    &.info {\n      background-color: $tooltip-info-color;\n      @include tooltip-pointer($tooltip-info-color);\n    }\n    &.warning {\n      background-color: $tooltip-warning-color;\n      @include tooltip-pointer($tooltip-warning-color);\n    }\n    &.success {\n      background-color: $tooltip-success-color;\n      @include tooltip-pointer($tooltip-success-color);\n    }\n\n    //Arrow positions\n    @include tooltip-pointer($tooltip-background);\n\n    &.top:before {\n      margin-bottom: -11px;\n      left: calc(50% - 6px);\n      bottom: 0;\n    }\n\n    &.top-left:before {\n      margin-right: 0;\n      margin-bottom: -11px;\n      right: 1px;\n      bottom: 0;\n    }\n\n    &.top-right:before {\n      margin-left: 0;\n      margin-bottom: -11px;\n      left: 1px;\n      bottom: 0;\n    }\n\n    &.bottom:before {\n      margin-top: -11px;\n      left: calc(50% - 6px);\n      top: 0;\n    }\n\n    &.bottom-left:before {\n      margin-right: 0;\n      margin-top: -11px;\n      right: 1px;\n      top: 0;\n    }\n\n    &.bottom-right:before {\n      margin-left: 0;\n      margin-top: -11px;\n      left: 1px;\n      top: 0;\n    }\n\n    &.left:before {\n      margin-right: -11px;\n      margin-bottom: -6px;\n      right: 0;\n      bottom: 50%;\n    }\n\n    &.right:before {\n      left: 0;\n      bottom: 50%;\n      margin-left: -11px;\n      margin-bottom: -6px;\n    }\n\n    &:before {\n      content: \"\";\n      position: absolute;\n      background: 0 0;\n      border: 6px solid transparent;\n      box-sizing: border-box;\n    }\n\n    // Sizing\n    &.extra-large,\n    &.large,\n    &.small,\n    &.medium {\n      white-space: normal;\n      line-height: 1.4em;\n      word-wrap: break-word;\n    }\n    &.extra-large {\n      width: 400px;\n      font-size: 1.2vh;\n    }\n    &.large {\n      width: 300px;\n    }\n    &.medium {\n      width: 150px;\n    }\n    &.small {\n      width: 80px;\n    }\n    &.preline {\n      white-space: pre-line;\n    }\n  }\n}\n","novo-slider {\n  display: block;\n  width: 100%;\n  max-width: 1000px;\n  margin: 0 auto;\n  position: relative;\n  overflow: hidden;\n\n  @for $i from 0 through 100 {\n    $offset: (#{$i * 100%});\n    $dist: (#{$i * -100%});\n\n    & > [slide=\"#{$i}\"] {\n      left: $offset;\n    }\n\n    &.slide-#{$i} {\n      .slides {\n        transform: translateX($dist);\n      }\n    }\n  }\n\n  .slides {\n    min-height: 250px;\n    transition: all 200ms ease-in-out;\n    width: 100%;\n    display: flex;\n    flex: 1 0 100%;\n\n    & > [slide] {\n      display: flex;\n      flex: 1 0 100%;\n      justify-content: center;\n      align-items: center;\n      flex-wrap: wrap;\n      flex-direction: column;\n    }\n\n    img {\n      display: block;\n      margin: auto;\n    }\n  }\n\n  .controls {\n    width: 100%;\n    position: absolute;\n    bottom: 10px;\n\n    button {\n      position: absolute;\n      top: 50%;\n      transform: translateY(-50%);\n\n      &[icon=\"previous\"] {\n        left: 10px;\n      }\n      &[icon=\"next\"],\n      &[icon=\"check\"] {\n        right: 10px;\n      }\n    }\n  }\n\n  .indicators {\n    display: flex;\n    flex-direction: row;\n    align-items: center;\n    justify-content: center;\n    width: 100%;\n    background: rgba(#fff, 0.05);\n    padding: 20px;\n\n    .indicator-circle {\n      width: 8px;\n      height: 8px;\n      margin: 0 5px;\n      border-radius: 50%;\n      background: #d8d8d8;\n      opacity: 0.2;\n      transition: all 200ms ease-in-out;\n\n      &.active {\n        opacity: 1;\n      }\n    }\n  }\n}\n","novo-tip-well {\n  &.active {\n    display: inline-block;\n    margin-bottom: $spacing-md;\n  }\n  > div {\n    display: inline-block;\n    border-radius: 0.25rem;\n    background-color: var(--background-main, $off-white);\n    color: var(--text-main, $dark);\n    padding: $spacing-lg;\n    text-align: right;\n    > div {\n      display: flex;\n      > i {\n        flex-shrink: 0;\n        text-align: center;\n        margin-top: 0.3rem;\n        margin-right: $spacing-md;\n        color: #aaaaaa;\n      }\n      > p {\n        @include novo-body-text();\n        width: 100%;\n        max-width: 40rem;\n        padding: 0;\n        text-align: left;\n        white-space: pre-line;\n      }\n    }\n  }\n}\n","novo-editor {\n  .cke {\n    font: inherit !important;\n    box-shadow: none;\n    border-color: var(--background-muted);\n\n    .cke_top,\n    .cke_bottom {\n      background: var(--background-muted) none;\n      box-shadow: none;\n    }\n\n    .cke_bottom {\n      border-top: none;\n    }\n  }\n}\n\n.cke_dialog_background_cover {\n  background-color: $black !important;\n}\n\n.cke_dialog {\n  .cke_dialog_title {\n    text-shadow: none;\n    background: var(--background-muted) none;\n    box-shadow: none;\n    border-bottom: none;\n  }\n\n  .cke_dialog_footer {\n    text-shadow: none;\n    background: var(--background-muted) none;\n    box-shadow: none;\n    border-top: none;\n  }\n}\n\n.theme-dark {\n  .cke_button {\n    filter: invert(1);\n  }\n  .cke_combo {\n    a.cke_combo_button {\n      &:hover,\n      &:active,\n      &:focus,\n      &.cke_combo_on {\n        background: var(--background-main);\n        border: 1px solid var(--border);\n      }\n      .cke_combo_text {\n        color: white;\n      }\n      .cke_combo_arrow {\n        border-top-color: white;\n      }\n    }\n  }\n}\n","novo-category-dropdown {\n  display: block;\n  position: relative;\n  button {\n    position: relative;\n    z-index: 0;\n  }\n  &.active {\n    .dropdown-container {\n      overflow: hidden;\n      max-height: inherit;\n      max-width: 400px;\n      min-width: 400px;\n      display: block;\n      z-index: z(\"max\");\n      border-radius: 2px;\n      top: 100%;\n      margin-top: 5px;\n    }\n  }\n  .dropdown-container {\n    background-color: white;\n    list-style: none;\n    line-height: 26px;\