  > novo-tab {\n        height: 30px;\n        min-height: 30px;\n        > .novo-tab-link {\n          max-width: 100%;\n          height: 100%;\n          font-size: 0.9em;\n          padding: 0.5em;\n          display: flex;\n          align-items: center;\n        }\n      }\n    }\n    novo-list {\n      max-height: 350px;\n      overflow: auto;\n    }\n    novo-list-item {\n      padding: 0.5em;\n      flex-shrink: 0;\n      cursor: pointer;\n      font-size: 0.9em;\n      &:focus,\n      &:hover {\n        background: lighten($light, 10%);\n        color: darken($light, 55%);\n        .novo-category-dropdown-hover {\n          display: block;\n        }\n      }\n      .novo-category-dropdown-hover {\n        display: none;\n        color: $positive;\n        font-size: 0.9em;\n        > i {\n          font-size: 0.9em;\n        }\n      }\n      &.novo-category-dropdown-empty-item {\n        pointer-events: none;\n      }\n    }\n  }\n  &[side=\"left\"] {\n    display: flex;\n    flex-direction: column;\n    align-items: flex-start;\n  }\n  &[side=\"right\"] {\n    display: flex;\n    flex-direction: column;\n    align-items: flex-end;\n    .dropdown-container {\n      right: 0;\n    }\n  }\n}\n","multi-picker {\n  @extend chips;\n  @extend novo-checkbox;\n\n  .chip-input-container {\n    padding-top: 10px;\n  }\n\n  ul.summary {\n    display: inline;\n    list-style: none;\n    color: darken(#d2d2d2, 30%);\n    padding: 0 10px 0;\n\n    li {\n      display: inline;\n      padding: 0 3px;\n    }\n\n    li:after {\n      content: \", \";\n    }\n\n    li:last-child:after {\n      content: \" \";\n    }\n  }\n\n  novo-picker {\n    li {\n      &.header {\n        text-transform: uppercase;\n        font-weight: 400;\n        border-top: 1px solid #e8e8e8;\n        padding-bottom: 0;\n      }\n      label {\n        color: #9e9e9e;\n        text-transform: capitalize;\n        &:hover {\n          i {\n            &.bhi-checkbox-empty,\n            &.bhi-checkbox-indeterminate {\n              color: $positive;\n            }\n          }\n        }\n      }\n      &.checked {\n        label {\n          color: darken(#d2d2d2, 60%);\n        }\n      }\n    }\n    i {\n      margin-right: 5px;\n\n      &.bhi-checkbox-empty {\n        color: #d2d2d2;\n      }\n\n      &.bhi-checkbox-filled,\n      &.bhi-checkbox-indeterminate {\n        color: $positive;\n      }\n    }\n  }\n\n  chip {\n    span {\n      text-transform: capitalize;\n    }\n  }\n}\n","@import \"../common/typography/text.mixins\";\n\npopover-content {\n  .popover {\n    position: absolute;\n    top: 0;\n    left: 0;\n    z-index: z(overlay);\n    display: none;\n    width: 40rem;\n    padding: 2rem;\n    background-color: var(--background-bright, $white);\n    color: var(--text-main, $dark);\n    background-clip: padding-box;\n    box-shadow: $shadow-popover;\n\n    &.top {\n      margin-top: -1rem;\n      &.virtual-area {\n        bottom: -1.1rem;\n      }\n    }\n    &.right {\n      margin-left: 1rem;\n      &.virtual-area {\n        left: -1.1rem;\n      }\n    }\n    &.bottom {\n      margin-top: 1rem;\n      &.virtual-area {\n        top: -1.1rem;\n      }\n    }\n    &.left {\n      margin-left: -1rem;\n      &.virtual-area {\n        right: -1.1rem;\n      }\n    }\n    .virtual-area {\n      height: 1.1rem;\n      width: 100%;\n      position: absolute;\n    } // Arrows\n    // .arrow is outer, .arrow:before is inner\n    &.top > .arrow {\n      margin-left: -9px;\n      border-bottom-width: 0;\n      border-top-color: rgba(0, 0, 0, 0.1);\n      bottom: -9px;\n      &:before {\n        content: \" \";\n        bottom: 1px;\n        margin-left: -1rem;\n        border-bottom-width: 0;\n        border-top-color: var(--background-bright, $white);\n      }\n      &.center {\n        left: 50%;\n      }\n      &.left {\n        left: 91%;\n      }\n      &.right {\n        left: 9%;\n      }\n    }\n    &.right > .arrow {\n      left: -9px;\n      margin-top: -9px;\n      border-left-width: 0;\n      border-right-color: rgba(0, 0, 0, 0.1);\n      &:before {\n        content: \" \";\n        left: 1px;\n        bottom: -1rem;\n        border-left-width: 0;\n        border-right-color: var(--background-bright, $white);\n      }\n      &.center {\n        top: 50%;\n      }\n      &.top {\n        top: 91%;\n      }\n      &.bottom {\n        top: 9%;\n      }\n    }\n    &.bottom > .arrow {\n      margin-left: -9px;\n      border-top-width: 0;\n      border-bottom-color: rgba(0, 0, 0, 0.1);\n      top: -9px;\n      &:before {\n        content: \" \";\n        top: 1px;\n        margin-left: -1rem;\n        border-top-width: 0;\n        border-bottom-color: var(--background-bright, $white);\n      }\n      &.center {\n        left: 50%;\n      }\n      &.left {\n        left: 91%;\n      }\n      &.right {\n        left: 9%;\n      }\n    }\n    &.left > .arrow {\n      right: -9px;\n      margin-top: -9px;\n      border-right-width: 0;\n      border-left-color: rgba(0, 0, 0, 0.1);\n      &:before {\n        content: \" \";\n        right: 1px;\n        border-right-width: 0;\n        border-left-color: var(--background-bright, $white);\n        bottom: -1rem;\n      }\n      &.center {\n        top: 50%;\n      }\n      &.top {\n        top: 91%;\n      }\n      &.bottom {\n        top: 9%;\n      }\n    }\n  }\n  .popover > .arrow {\n    border-width: 9px;\n    &,\n    &:before {\n      position: absolute;\n      display: block;\n      width: 0;\n      height: 0;\n      border-color: transparent;\n      border-style: solid;\n    }\n    &:before {\n      border-width: 1rem;\n      content: \"\";\n    }\n  }\n  .popover-title {\n    @include novo-title-text();\n    margin-bottom: 1rem;\n  }\n  .popover-content {\n    @include novo-body-text();\n    .popover-content-text {\n      white-space: pre-line;\n    }\n  }\n}\n","novo-dropdown-cell,\n.novo-table-dropdown-cell {\n  > novo-dropdown > button[theme][theme=\"second